@import url(https://fonts.googleapis.com/css?family=Palanquin+Dark|Ubuntu);
* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
}

a:link {
  text-decoration: none !important;
}

button {
  outline: none;
  border: none;
}

.nav-bar {
  width: 100%;
  height: 70px;
  background-color: #ff004f;
  display: flex;
  align-items: center;
  position: relative;
}

.logo {
  font-size: 36px;
  padding: 0 48px;
  margin-right: auto;
}

.logo > a {
  text-decoration: none;
  color: #ffffff;
  text-shadow: 3px 3px #b7012b;
  font-family: "Palanquin Dark", sans-serif;
}

.greeting {
  color: #ffffff;
  text-shadow: 2px 2px #b7012b;
  font-family: "Palanquin Dark", sans-serif;
  font-size: 24px;
  margin-right: 10px;
}

.buttons {
  display: flex;
  margin-right: 50px;
}

.menu-button {
  display: none;
}

.nav-button {
  font-size: 18px;
  border-radius: 22.5px;
  margin: 0 10px;
  padding: 10px 20px;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  background-color: #ffffff;
}

.nav-button:focus,
.nav-button:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

@media (min-width: 720px) and (max-width: 1023px) {
  .logo {
    padding: 0 32px;
  }

  .nav-button {
    background-color: #ffffff;
  }

  .buttons {
    margin-right: 16px;
  }
}

@media (min-width: 480px) and (max-width: 719px) {
  .logo {
    padding: 0 28px;
  }

  .greeting {
    display: none;
  }

  .buttons {
    margin-right: 16px;
  }
}

@media (max-width: 479px) {
  .logo {
    font-size: 32px;
    padding: 0 24px;
  }

  .greeting,
  .buttons {
    display: none;
  }

  .menu-button {
    display: block;
    margin: 0 16px;
    background-color: rgba(255, 255, 255, 0);
  }

  .menu-button > img {
    width: 40px;
  }

  .mobile-buttons {
    display: flex;
    flex-wrap: wrap;
    z-index: 10;
    width: 100%;
    height: 100vh;
    position: fixed;
  }

  .mobile-btn-wrap {
    width: 100%;
  }

  .mobile-btn {
    width: 100%;
    font-size: 28px;
    background-color: rgba(255, 255, 255, 0.9);
    text-align: center;
    padding: 8px;
    border: 1px solid #3a3a3a;
    height: 50px;
  }
}

.auth-view {
  height: 100%;
  width: 100%;
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: center;
}

.auth-wrap {
  position: absolute;
  top: 70px;
  width: 500px;
  display: flex;
  background-color: #f5f5f5;
}

.signup-form,
.login-form {
  font-size: 20px;
  width: 80%;
  margin: 10% auto;
}

.exit-btn {
  position: absolute;
  top: 10px;
  right: 12px;
  font-size: 32px;
}

.signup-form > div .login-form > div {
  margin: 12px 0;
}

input {
  font-size: 18px;
  padding: 5px 10px;
  border-radius: 10px;
  width: 100%;
  height: 36px;
  margin: 5px 0;
  outline: none;
  border: none;
}

input:focus {
  border: 1px solid rgba(135, 135, 135, 0.8);
}

.submit-btn {
  height: 36px;
  width: 90px;
  font-size: 18px;
  margin: 12px 0;
  background-color: #418de5;
  color: #ffffff;
  border: none;
  cursor: pointer;
}

@media (min-width: 480px) and (max-width: 719px) {
  .auth-wrap {
    width: 70%;
  }

  .signup-form,
  .login-form {
    font-size: 20px;
    width: 85%;
    margin: 10% auto;
  }

  input {
    font-size: 16px;
  }
}

@media (max-width: 479px) {
  .auth-wrap {
    width: 80%;
  }

  .signup-form,
  .login-form {
    font-size: 20px;
    width: 85%;
    margin: 10% auto;
  }
  
  input {
    font-size: 16px;
  }
}

.banner {
  width: 100%;
  min-height: calc(100vh - 70px);
  background-image: url(/KEYDOWN/static/media/dj.130cf962.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.enter-wrap {
  position: absolute;
  top: 70%;
  left: 0;
  right: 0;
  display: flex;
}

.enter-btn {
  margin: auto;
  color: rgba(0, 0, 0, 0.9);
  font-size: 3rem;
  background: rgba(255, 255, 255, 0.8);
  padding: 16px 32px;
  border-radius: 49.5px;
  cursor: pointer;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.1s -webkit-transform;
  transition: 0.1s -webkit-transform;
  transition: 0.1s transform;
  transition: 0.1s transform, 0.1s -webkit-transform;
  text-decoration: none;
}

.enter-btn:hover {
  color: rgba(0, 0, 0, 1);
  background: rgba(255, 255, 255, 1);
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

.enter-btn > a {
  text-decoration: none;
  color: #000000;
}

@media (max-width: 479px) {
  .enter-btn {
    font-size: 2.2rem;
  }
}

.song-list-wrap {
  background-color: #1d1d1d;
  width: 100%;
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.song-list {
  max-width: 1200px;
  width: 70%;
  margin-top: 36px;
}

.song {
  background-color: #fffaf8;
  border: 1px solid #878787;
  border-radius: 10px;
  padding: 10px;
  width: 100%;
  margin: 40px auto;
  display: flex;
  align-items: center;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.1s -webkit-transform;
  transition: 0.1s -webkit-transform;
  transition: 0.1s transform;
  transition: 0.1s transform, 0.1s -webkit-transform;
}

.song:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.song-details {
  margin-left: 30px;
  margin-right: auto;
}

.song-title {
  font-size: 26px;
}

.song-auth {
  font-size: 18px;
  margin-top: 10px;
}

.difficulty-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 40px;
}

.difficulty {
  width: 80px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 20px;
  font-size: 16px;
  color: #fffaf8;
  border: none;
  background-color: rgba(255, 0, 79, 0.5);
  border-radius: 5px;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: 0.1s -webkit-transform;
  transition: 0.1s -webkit-transform;
  transition: 0.1s transform;
  transition: 0.1s transform, 0.1s -webkit-transform;
  cursor: pointer;
}

.difficulty:hover {
  background-color: rgba(255, 0, 79, 1);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media (min-width: 720px) and (max-width: 1023px) {
  .song-list {
    width: 80%;
  }

  .song-details {
    margin-left: 30px;
  }

  .song-title {
    font-size: 24px;
  }

  .song-auth {
    font-size: 18px;
    margin-top: 10px;
  }

  .difficulty-wrap {
    margin: 0;
  }
}

@media (min-width: 480px) and (max-width: 719px) {
  .song-list {
    width: 90%;
    margin-top: 16px;
  }

  .song {
    width: 100%;
    flex-wrap: wrap;
    margin: 30px auto;
  }

  .song-details {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 5px 10px;
    margin: 0;
  }

  .song-title {
    width: 50%;
    font-size: 18px;
  }

  .song-auth {
    width: 50%;
    font-size: 16px;
    margin-top: 0px;
  }

  .difficulty-wrap {
    width: 100%;
    margin: 10px 10px 0;
    justify-content: space-between;
  }

  .difficulty-wrap > a {
    width: 25%;
  }

  .difficulty {
    width: 100%;
    height: 40px;
    font-size: 12px;
    margin: 0px;
  }
}

@media (max-width: 479px) {
  .song-list {
    margin-top: 12px;
    width: 90%;
  }

  .song {
    width: 100%;
    flex-wrap: wrap;
    margin: 30px auto;
  }

  .song-details {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    padding: 5px;
    margin: 0;
  }

  .song-title {
    width: 100%;
    font-size: 18px;
  }

  .song-auth {
    width: 100%;
    font-size: 16px;
    margin-top: 5px;
  }

  .difficulty-wrap {
    width: 100%;
    margin: 0px;
    justify-content: space-between;
  }

  .difficulty-wrap > a {
    width: 30%;
  }
  
  .difficulty {
    width: 100%;
    height: 40px;
    font-size: 12px;
    margin: 0px;
  }
}

.game-view {
  width: 100%;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  background-color: #1d1d1d;
}

#bg-canvas {
  width: 100%;
  height: 100%;
  position: fixed;
}

.battle {
  color: #ffffff;
  display: flex;
  font-size: 28px;
  justify-content: space-between;
  font-family: "Ubuntu", sans-serif;
  margin-top: 24px;
}

.battle-board {
  text-align: center;
  width: 48%;
  border: 1px solid #878787;
  padding: 10px 0;
}

.score-in-board {
  padding-top: 12px;
}

.canvas-wrap {
  position: relative;
  margin-top: 24px;
}

#game-canvas {
  margin: 0;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
}

#player-canvas {
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.game-buttons {
  width: 100%;
  display: flex;
}

.game-btn {
  width: 25%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -5px;
  font-size: 36px;
  font-weight: bold;
  border-radius: 15px;
  color: #ffffff;
  font-family: "Palanquin Dark", sans-serif;
}

.btn-d {
  background-color: #3498db;
  border-bottom: 5px solid #2980b9;
  text-shadow: 0px -2px #2980b9;
}

.btn-f {
  background-color: #e74c3c;
  border-bottom: 5px solid #bd3e31;
  text-shadow: 0px -2px #bd3e31;
}

.btn-k {
  background-color: #9fff5b;
  border-bottom: 5px solid #7ecc47;
  text-shadow: 0px -2px #7ecc47;
}

.btn-l {
  background-color: #f9f524;
  border-bottom: 5px solid #cecb18;
  text-shadow: 0px -2px #cecb18;
}

.btn-d-active {
  border-bottom: 2px solid #2980b9;
}

.btn-f-active {
  border-bottom: 2px solid #bd3e31;
}

.btn-k-active {
  border-bottom: 2px solid #7ecc47;
}

.btn-l-active {
  border-bottom: 2px solid #cecb18;
}

.btn-d:active {
  border-bottom: 2px solid #2980b9;
}

.btn-f:active {
  border-bottom: 2px solid #bd3e31;
}

.btn-k:active {
  border-bottom: 2px solid #7ecc47;
}

.btn-l:active {
  border-bottom: 2px solid #cecb18;
}

@media (min-width: 480px) and (max-width: 719px) {
  .battle {
    font-size: 26px;
  }

  .game-btn {
    font-size: 18px;
    height: 50px;
    border-radius: 10px;
  }
}

@media (max-width: 479px) {
  .battle {
    margin-top: 16px;
    font-size: 18px;
  }

  .game-btn {
    font-size: 18px;
    height: 40px;
    border-radius: 10px;
  }
}

.ranking-view {
  width: 100%;
  display: flex;
  min-height: 100vh;
  justify-content: center;
  background-color: #1d1d1d;
  font-family: "Ubuntu", sans-serif;
}

.ranking-wrap {
  width: 80%;
  max-width: 1200px;
  display: flex;
  margin-top: 36px;
  color: #ffffff;
}

.current-ranking {
  width: 33%;
  font-size: 32px;
}

.current-ranking-row {
  width: 90%;
  display: flex;
  justify-content: space-between;
  margin: 18px 0;
}

.record {
  margin: 0 auto;
  width: 66%;
  font-size: 18px;
}

.record-board {
  display: flex;
  justify-content: space-evenly;
  padding: 10px 0;
  text-align: center;
  border-bottom: 1px solid #ffffff;
}

.title {
  border-bottom: 1px solid #ffffff;
  padding: 18px 0;
  font-weight: bolder;
  font-size: 24px;
}

.record-item:nth-child(1) {
  width: 20%;
}

.record-item:nth-child(2) {
  width: 20%;
}

.record-item:nth-child(3) {
  width: 20%;
}

.record-item:nth-child(4) {
  width: 40%;
}

@media (min-width: 720px) and (max-width: 1023px) {
  .ranking-wrap {
    width: 90%;
  }

  .current-ranking {
    width: 40%;
    font-size: 26px;
  }
}

@media (min-width: 480px) and (max-width: 719px) {
  .ranking-wrap {
    flex-wrap: wrap;
    width: 90%;
  }

  .current-ranking {
    width: 100%;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    margin: 0 auto;
    border: 1px solid #979797;
    border-radius: 10px;
    padding: 0 40px;
    font-size: 20px;
  }

  .current-ranking-row {
    width: 100%;
  }

  .title {
    font-size: 24px;
  }

  .record {
    width: 100%;
    font-size: 16px;
  }

  .record-board {
    width: 100%;
  }
}

@media (max-width: 479px) {
  .ranking-wrap {
    flex-wrap: wrap;
    width: 90%;
  }

  .current-ranking {
    width: 100%;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    border: 1px solid #979797;
    border-radius: 15px;
    padding: 0 30px;
    font-size: 18px;
  }

  .current-ranking-row {
    width: 100%;
  }

  .title {
    font-size: 20px;
  }

  .record {
    width: 100%;
    font-size: 16px;
  }

  .record-board {
    width: 100%;
  }
}

